


















import Vue from "vue"
import Component from "vue-class-component"
import BeerCard from "@/components/BeerCard.vue"

@Component({
  components: { BeerCard },
})
export default class ListBeers extends Vue {
  async created() {
    await this.$store.dispatch("loadBeers")
  }
  data() {
    return this.$store.state
  }
}
